@use 'vars' as *;

.company-detail{
	padding-bottom: 30px;
	&__wp{
		@include flex(between, 0);
		@include font-size(16);
	}
	&__field{
		margin-bottom: 24px;
		font-family: $font-Monterrat;
		&-label{
			@include font-size(14);
			line-height: 1.4;
			color: #64748B;
			margin-bottom: 6px;
			font-weight: 600;
		}
		&-text{
			font-weight: 500;
			@include fts(16, 25.6);
			.label-status{
				@include font-size(14);
				padding: 5px;
				max-width: 135px;
			}
		}
		&-shortUrl{
			@include flex(0, center);
			a{
				color: #3491FF;
				margin-left: 12px;
				font-weight: 600;
			}
		}
	}
}


.manpower-page{
	&__control{
		@include flex(between, center);
		padding: 16px 30px;
	}
	&__search{
		width: calc(100% - 140px);
		padding-right: 10px;
		@include flex(0, center);
		&-field{
			max-width: 324px;
			width: 100%;
			margin-right: 30px;
			&:last-of-type{
				margin-right: 0;
			}
			&--company-name{
				position: relative;
				@include p-before(20px, 20px){
					background: url(../img/common/search-normal.png) no-repeat center/100%;
					top: calc(50% - 10px);
					left: 10px;
				}
				.form-control{
					padding-left: 44px;
				}
			}
			&--status{
				max-width: 230px;
			}
			.form-control--select{
				background: #fff url(../img/common/chevron-down.png) no-repeat right 15px center/24px 24px;
			}
		}
	}
	&__wp{
		padding: 0 30px;
	}
	&__footer{
		@include flex(between, center);
	}
	&__left{
		@include font-size(14);
		strong{
			font-weight: 700;
			padding: 0 5px;
		}
	}
	&__right{
		@include flex(end, center);
		gap: 0 60px;
		width: 80%;
	}
	&__pagination{
		@include flex(0, 0, wrap);
		gap: 0 10px;
		.page{
			a{
				width: 36px;
				height: 36px;
				border: 1px solid #B1B5C3;
				border-radius: 8px;
				@include flex(center, center);
				@include font-size(14);
			}
			
			&.current{
				a{
					background: linear-gradient(90deg, #F5601B 0%, #FF862A 100%);
					border-color: transparent;
					color: #fff;
				}
			}
		}
		.extend{
			width: 36px;
			height: 36px;
			@include flex(center, center);
			@include p-before(16px, 16px){
				background: url(../img/common/extend-pagination.png) no-repeat center/100%;
				position: relative;
			}
		}
	}
	&__postperpage{
		max-width: 104px;
		width: 100%;
		.form-control{
			cursor: pointer;
			height: 32px;
			padding: 0 16px;
			@include font-size(12);
			color: #000;
			background: url(../img/common/chevron-down.png) no-repeat center right 8px/16px 16px;
			@include fn-hover;
		}
	}
	&__goto{
		max-width: 140px;
		width: 100%;
		@include flex(0, center);
		@include font-size(14);
		white-space: nowrap;
		.form-control--sm{
			width: 50px;
			height: 32px;
			text-align: center;
			margin-left: 8px;
			padding: 0 10px;
		}
	}
}



.company-featured{
	font-family: $font-Averta-Rugular;
    @include flex();
    &__sidebar{
		width: 320px;
		background: #fff;
		padding: 24px 16px;
		min-height: 100vh;
		box-shadow: 0px 0px 8px 0px #2955AA1C;
		&-ttl{
			@include font-size(20);
			font-weight: 700;
			position: relative;
			margin-bottom: 24px;
			.btn-add{
				width: 24px;
				height: 24px;
				position: absolute;
				top: calc(50% - 12px);
				right: 0;
				padding: 0;
				outline: 0;
				background: transparent;
				border: none;
				cursor: pointer;
				@include fn-hover;
			}
		}
    }
	&__main{
		width: calc(100% - 320px);
		&-wp{
			padding: 16px 30px;
		}
	}
}


.category-list{
	li{
		height: 48px;
		@include font-size(16);
		position: relative;
		margin-bottom: 24px;
		@include flex(0, center);
		padding: 0 16px;
		border-radius: 8px;
		cursor: pointer;
		@include smooth-transition;
		@include p-after(100%, 1px){
			background: #E2E8F0;
			bottom: -12px;
			left: 0;
		}
		&:hover{
			background-color: #F6F8FC;
			.category-list__edit{
				opacity: 1;
				visibility: visible;
			}
		}
		span{
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
			width: 168px;
		}
		.c-switch{
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			right: 0;
			div{
				padding-left: 48px;
			}
		}
		&:first-child{
			font-weight: 700;
			color: $color-main;
		}
	}
	&__edit{
		width: 24px;
		height: 24px;
		background: url(../img/common/edit-2.png) no-repeat center/100%;
		outline: 0;
		border: none;
		cursor: pointer;
		@include fn-hover;
		position: absolute;
		top: 50%;
		right: 55px;
		transform: translateY(-50%);
		opacity: 0;
		visibility: hidden;
		@include smooth-transition;
	}
}

.box-add-cate{
	@include flex();
	gap: 0 30px;
	padding: 16px 30px;
	.form-control{
		width: 500px;
		font-weight: 400;
	}
	.c-btn__01{
		font-weight: 400;
	}
}